import { NextRequest, NextResponse } from 'next/server'
import { createClient } from '@/lib/supabase/server'
import type { 
  Query, 
  CreateQueryRequest, 
  QueriesListResponse, 
  QueryResponse 
} from '@/types/query'

// GET - Fetch all queries for the authenticated user
export async function GET(request: NextRequest) {
  try {
    const supabase = await createClient()
    
    // Get the authenticated user
    const { data: { user }, error: authError } = await supabase.auth.getUser()
    
    if (authError || !user) {
      return NextResponse.json(
        { success: false, error: 'Unauthorized', queries: [], count: 0 },
        { status: 401 }
      )
    }

    // Get pagination parameters from query string
    const searchParams = request.nextUrl.searchParams
    const limit = parseInt(searchParams.get('limit') || '50')
    const offset = parseInt(searchParams.get('offset') || '0')
    const queryType = searchParams.get('type') // Optional filter by query type

    // Build query
    let query = supabase
      .from('queries')
      .select('*', { count: 'exact' })
      .eq('user_id', user.id)
      .order('created_at', { ascending: false })
      .range(offset, offset + limit - 1)

    // Apply optional type filter
    if (queryType) {
      query = query.eq('query_type', queryType)
    }

    const { data, error, count } = await query

    if (error) {
      console.error('Error fetching queries:', error)
      return NextResponse.json(
        { success: false, error: error.message, queries: [], count: 0 },
        { status: 500 }
      )
    }

    const response: QueriesListResponse = {
      success: true,
      queries: (data || []) as Query[],
      count: count || 0
    }

    return NextResponse.json(response, { status: 200 })
  } catch (error) {
    console.error('Error in GET /api/queries:', error)
    return NextResponse.json(
      { 
        success: false, 
        error: error instanceof Error ? error.message : 'Internal server error',
        queries: [],
        count: 0
      },
      { status: 500 }
    )
  }
}

// POST - Create a new query
export async function POST(request: NextRequest) {
  try {
    const supabase = await createClient()
    
    // Get the authenticated user
    const { data: { user }, error: authError } = await supabase.auth.getUser()
    
    if (authError || !user) {
      return NextResponse.json(
        { success: false, error: 'Unauthorized' },
        { status: 401 }
      )
    }

    // Parse request body
    const body: CreateQueryRequest = await request.json()
    
    // Validate request body
    if (!body.query_type || !body.details) {
      return NextResponse.json(
        { success: false, error: 'Missing required fields: query_type and details' },
        { status: 400 }
      )
    }

    // Validate query_type
    const validTypes = [
      'AI Prediction - Player Prop',
      'AI Prediction - Game Outcome',
      'Player Stats',
      'Player Comparison'
    ]
    
    if (!validTypes.includes(body.query_type)) {
      return NextResponse.json(
        { success: false, error: 'Invalid query_type' },
        { status: 400 }
      )
    }

    // Add timestamp to details if not present
    const detailsWithTimestamp = {
      ...body.details,
      timestamp: body.details.timestamp || new Date().toISOString()
    }

    // Insert query into database
    const { data, error } = await supabase
      .from('queries')
      .insert({
        user_id: user.id,
        query_type: body.query_type,
        details: detailsWithTimestamp
      })
      .select()
      .single()

    if (error) {
      console.error('Error creating query:', error)
      return NextResponse.json(
        { success: false, error: error.message },
        { status: 500 }
      )
    }

    const response: QueryResponse = {
      success: true,
      query: data as Query
    }

    return NextResponse.json(response, { status: 201 })
  } catch (error) {
    console.error('Error in POST /api/queries:', error)
    return NextResponse.json(
      { 
        success: false, 
        error: error instanceof Error ? error.message : 'Internal server error' 
      },
      { status: 500 }
    )
  }
}

// DELETE - Delete a specific query (optional, for future use)
export async function DELETE(request: NextRequest) {
  try {
    const supabase = await createClient()
    
    // Get the authenticated user
    const { data: { user }, error: authError } = await supabase.auth.getUser()
    
    if (authError || !user) {
      return NextResponse.json(
        { success: false, error: 'Unauthorized' },
        { status: 401 }
      )
    }

    // Get query ID from URL
    const searchParams = request.nextUrl.searchParams
    const queryId = searchParams.get('id')

    if (!queryId) {
      return NextResponse.json(
        { success: false, error: 'Query ID is required' },
        { status: 400 }
      )
    }

    // Delete query (RLS policy ensures user can only delete their own queries)
    const { error } = await supabase
      .from('queries')
      .delete()
      .eq('id', queryId)
      .eq('user_id', user.id)

    if (error) {
      console.error('Error deleting query:', error)
      return NextResponse.json(
        { success: false, error: error.message },
        { status: 500 }
      )
    }

    return NextResponse.json(
      { success: true },
      { status: 200 }
    )
  } catch (error) {
    console.error('Error in DELETE /api/queries:', error)
    return NextResponse.json(
      { 
        success: false, 
        error: error instanceof Error ? error.message : 'Internal server error' 
      },
      { status: 500 }
    )
  }
}
